.navbar {
  display: flex;
  background-color: #466099;
  justify-content: flex-end;
  flex-direction: column;
}

.bar {
  display: flex;
  justify-content: space-between;
}

.logo {
  display: flex;
  align-items: center;
}

.logo > a {
  font-size: 1.4em;
  font-weight: bold;
}

.menu {
  display: flex;
  flex-direction: column;
}

.fixed-navbar {
  position: fixed;
  width: 100%;
  z-index: 1;
}

.menu > a {
  background-color: #698fa9;
  color: #ffffff;
  padding: 10px;
  text-align: center;
  text-decoration: none;
}

.menu > a:hover {
  background-color: #89a7bc;
}

.bar-item {
  height: 45px;
}

.toggle-menu {
  width: 50px;
  padding: 5px;
  cursor: pointer;
}

.toggle-menu > span {
  display: block;
  width: 100%;
  height: 5px;
  background-color: #ffffff;
  margin-bottom: 5px;
}

.clicked > span:nth-child(2n) {
  visibility: hidden;
}

.clicked > span:first-child {
  transform: rotate(45deg);
  position: relative;
  top: 15px;
}

.clicked > span:last-child {
  transform: rotate(135deg);
  position: relative;
  top: -6px;
}

.close-menu {
  display: none;
}

/* desktop */
@media (min-width: 992px) {
  .toggle-menu {
    display: none;
  }
  .menu {
    display: flex;
    flex-direction: row;
    width: fit-content;
  }
  .navbar {
    justify-content: space-between;
    flex-direction: inherit;
  }
}

